import React from "react";
import '../../styles/home.css'
import logo from "../../images/logo.png";
import flowers from "../../images/flores.svg";
import PDF from "../../images/juani-sabbatini-cv.pdf";
import Header from "../components/Header";
import ukFlag from "../../images/uk-flag.png";
import { Link } from "react-router-dom"

export default function Home() {
  document.title = "Juani Sabbatini";
  return (
    <div>
      <Header />
      <Link to="/en" className="language-flag">English <img src={ukFlag} alt="English" /></Link>
      <div className="Home">
        <div className="name">
          <h1>HOLA, SOY JUANI SABBATINI</h1>
          <p>
            Desarrollador Fullstack en Javascript con una gran pasión por la
            programación.
          </p>
          <a href={PDF} download={PDF}>
            <button type="button">Mira mi CV</button>
          </a>
        </div>
        <img src={logo} className="developer" alt="Developer" />
        <img className="flowers" src={flowers} alt="Flowers" />
      </div>
    </div>
  );
}
