import React from "react";
import { Link } from "react-router-dom";
import portfolio from "../../../images/portfolio.png";
import juaniflix from "../../../images/juaniflix.png";
import simonSays from "../../../images/simon-says.png";
import marioLuigi from "../../../images/mario-luigi.png";
import covidApi from "../../../images/covidapi.png";
import "../../../styles/projects.css";
import Header from "../../components/Header";
import spainFlag from "../../../images/spain-flag.png";

export default function Projects() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/projects" className="language-flag">Español <img src={spainFlag} alt="Spanish" /></Link>
      <div className="Projects">
        <div className="container">
          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>01</h2>
              <h3>PORTFOLIO</h3>
              <img src={portfolio} alt="Juaniflix" />
              <p>
                This is my personal portfolio, where there are some of my
                projects and the information about me.
              </p>
              <Link to="/en/projects/portfolio">SEE MORE</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>02</h2>
              <h3>NETFLIX CLONE</h3>
              <img src={juaniflix} alt="Juaniflix" />
              <p>
                Juaniflix it´s a Netflix clone created with React js, Omdb Api
                and Material UI.
              </p>
              <Link to="/en/projects/juaniflix">SEE MORE</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>03</h2>
              <h3>SIMON SAYS</h3>
              <img src={simonSays} alt="Simon Says" />
              <p>
                This is my personal portfolio, where there are some of my
                projects and the information about me.
              </p>
              <Link to="/en/projects/simon-says">SEE MORE</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>04</h2>
              <h3>MARIO VS LUIGI</h3>
              <img src={marioLuigi} alt="Mario vs Luigi Pong" />
              <p>
                Mario vs Luigi Pong is my adaptation of the famous game. It
                consist in a 1 vs 1 game created with HTML, CSS and JavaScript.
              </p>
              <Link to="/en/projects/mario-vs-luigi">SEE MORE</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>05</h2>
              <h3>COVID LIVE DATA</h3>
              <img src={covidApi} alt="Covid Live Data" />
              <p>
                Covid19 Live Data is a website that shows all the information
                about the covid in every country in real time using the Covid19
                API.
              </p>
              <Link to="/en/projects/covid-live-data">SEE MORE</Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
