import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import "./App.css";

import EnHome from "./en/pages/Home";
import EnAboutMe from "./en/pages/AboutMe";
import EnProjects from "./en/pages/Projects/Projects";
import EnContact from "./en/pages/Contact";
import EnPortfolio from "./en/pages/Projects/Portfolio";
import EnJuaniflix from "./en/pages/Projects/Juaniflix";
import EnMarioLuigi from "./en/pages/Projects/MarioLuigi";
import EnSimonSays from "./en/pages/Projects/SimonSays";
import EnCovidApi from "./en/pages/Projects/CovidApi";

import Home from "./es/pages/Home";
import AboutMe from "./es/pages/AboutMe";
import Projects from "./es/pages/Projects/Projects";
import Contact from "./es/pages/Contact";
import Portfolio from "./es/pages/Projects/Portfolio";
import Juaniflix from "./es/pages/Projects/Juaniflix";
import MarioLuigi from "./es/pages/Projects/MarioLuigi";
import SimonSays from "./es/pages/Projects/SimonSays";
import CovidApi from "./es/pages/Projects/CovidApi";
import Error404 from "./es/pages/Error404";

export default function App() {

  return (
      <div className="App">
        <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/about-me" component={AboutMe} />
        <Route exact path="/projects" component={Projects} />
        <Route exact path="/projects/portfolio" component={Portfolio} />
        <Route exact path="/projects/juaniflix" component={Juaniflix} />
        <Route exact path="/projects/mario-vs-luigi" component={MarioLuigi} />
        <Route exact path="/projects/simon-says" component={SimonSays} />
        <Route exact path="/projects/covid-live-data" component={CovidApi} />
        <Route exact path="/contact" component={Contact} />
        <Route exact path="/404" component={Error404} />

        <Route exact path="/en/" component={EnHome} />
        <Route exact path="/en/about-me" component={EnAboutMe} />
        <Route exact path="/en/projects" component={EnProjects} />
        <Route exact path="/en/projects/portfolio" component={EnPortfolio} />
        <Route exact path="/en/projects/juaniflix" component={EnJuaniflix} />
        <Route exact path="/en/projects/mario-vs-luigi" component={EnMarioLuigi} />
        <Route exact path="/en/projects/simon-says" component={EnSimonSays} />
        <Route exact path="/en/projects/covid-live-data" component={EnCovidApi} />
        <Route exact path="/en/contact" component={EnContact} />
        <Route exact path="/en/404" component={Error404} />
        <Redirect from="*" to="/404"/>
        </Switch>
      </div>
  );
}
