import React from "react";
import logo from "../../images/logo.png";
import flowers from "../../images/flores.svg";
import PDF from "../../images/juani-sabbatini-cv.pdf";
import "../../styles/home.css";
import Header from "../components/Header";
import spainFlag from "../../images/spain-flag.png";
import { Link } from "react-router-dom"

export default function Home() {
  document.title = "Juani Sabbatini";
  return (
    <div>
      <Header />
      <Link to="/" className="language-flag">Español <img src={spainFlag} alt="Spanish" /></Link>
      <div className="Home">
        <div className="name">
          <h1>HI, I'M JUANI SABBATINI</h1>
          <p>
            Fullstack developer in Javascript with a great passion for coding.
          </p>
          <a href={PDF} download={PDF}>
            <button type="button">See my CV</button>
          </a>
        </div>
        <img src={logo} className="developer" alt="Developer" />
        <img className="flowers" src={flowers} alt="Flowers" />
      </div>
    </div>
  );
}
