import React from "react";
import marioLuigi from "../../../images/mario-luigi.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import "../../../styles/projects.css";
import Header from "../../components/Header";
import spainFlag from "../../../images/spain-flag.png";
import { Link } from "react-router-dom"

export default function MarioLuigi() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/projects/mario-vs-luigi" className="language-flag">Español <img src={spainFlag} alt="Spanish" /></Link>
      <div className="Project">
        <img
          src={marioLuigi}
          alt="Mario vs Luigi Pong"
          className="project-image"
        />
        <div className="project-info">
          <h3 className="project-text">
          Mario vs Luigi Pong is my adaptation of the famous game. It consist in
          a 1 vs 1 game created with HTML, CSS and JavaScript.
            <br />
            <br />
            <span className="languages">HTML</span>
            <span className="languages">CSS</span>
            <span className="languages">JavaScript</span>
          </h3>
          <div className="buttons-project">
            <a
              href="https://github.com/juanisabba/mario-vs-luigi/"
              target="_blank"
              rel="noreferrer"
            >
              See it on GitHub{" "}
              <FontAwesomeIcon
                icon={faGithub}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
            <a
              href="https://juanisabba.github.io/mario-vs-luigi/"
              target="_blank"
              rel="noreferrer"
            >
              See the website{" "}
              <FontAwesomeIcon
                icon={faExternalLinkAlt}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
