import React from "react";
import '../../styles/about-me.css'
import profile from "../../images/profile-pic.jpg";
import Header from "../components/Header";
import ukFlag from "../../images/uk-flag.png";
import { Link } from "react-router-dom"

export default function AboutMe() {
  document.title = "Juani Sabbatini | About me";
  return (
    <div>
      <Header />
      <Link to="/en/about-me" className="language-flag">English <img src={ukFlag} alt="English" /></Link>
      <div className="About-me">
        <div className="about-me-grid">
          <div className="juani-info">
            <div className="information">
              <h1>Sobre mí</h1>
              Hola, mi nombre es Juan Ignacio Sabbatini, soy un desarrollador
              junior fullstack en Javascript, dominando tanto el frontend con
              React y el Backend con Node. Estoy en busca de mi primer empleo
              como desarrollador web para poner en práctica todos mis
              conocimientos y poder continuar aprendiendo día a día.
            </div>
            <div className="skills">
              <h1>Conocimientos</h1>
              <div className="skills-list">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>SASS</li>
                  <li>Bootstrap</li>
                </ul>
                <ul>
                  <li>JavaScript</li>
                  <li>React JS</li>
                  <li>Git / GitHub</li>
                </ul>
                <ul>
                  <li>Node JS</li>
                  <li>Express</li>
                  <li>MongoDB</li>
                </ul>
              </div>
            </div>
          </div>
          <img className="profile-pic" src={profile} alt="" />
        </div>
      </div>
    </div>
  );
}
