import React from "react";
import profile from "../../images/profile-pic.jpg";
import spainFlag from "../../images/spain-flag.png";
import { Link } from "react-router-dom"
import Header from "../components/Header";
import "../../styles/about-me.css";

export default function AboutMe() {
  document.title = "Juani Sabbatini | About me";
  return (
    <div>
      <Header />
      <Link to="/about-me" className="language-flag">Español <img src={spainFlag} alt="Spanish" /></Link>
      <div className="About-me">
        <div className="about-me-grid">
          <div className="juani-info">
            <div className="information">
              <h1>About me</h1>
              Hi, my name is Juan Ignacio Sabbatini, I'm a fullstack junior
              developer in Javascript, using both the frontend with React
              and the Backend with Node. I'm looking for my first job as a web
              developer to put all my knowledge into practice and be able to
              continue learning every day.
            </div>
            <div className="skills">
              <h1>Skills</h1>
              <div className="skills-list">
                <ul>
                  <li>HTML5</li>
                  <li>CSS3</li>
                  <li>SASS</li>
                  <li>Bootstrap</li>
                </ul>
                <ul>
                  <li>JavaScript</li>
                  <li>React JS</li>
                  <li>Git / GitHub</li>
                </ul>
                <ul>
                  <li>Node JS</li>
                  <li>Express</li>
                  <li>MongoDB</li>
                </ul>
              </div>
            </div>
          </div>
          <img className="profile-pic" src={profile} alt="" />
        </div>
      </div>
    </div>
  );
}
