import React from "react";
import '../../../styles/projects.css'
import simonSays from "../../../images/simon-says.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import Header from "../../components/Header";
import ukFlag from "../../../images/uk-flag.png";
import { Link } from "react-router-dom"

export default function SimonSays() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/en/projects/simon-says" className="language-flag">English <img src={ukFlag} alt="English" /></Link>
      <div className="Project">
        <img
          src={simonSays}
          alt="Simon Says"
          className="project-image"
        />
        <div className="project-info">
          <h3 className="project-text">
            El popular juego donde tenés que recordar el color y el orden.
            Creado con HTML, CSS y JavaScript. <br />
            <br />
            <span className="languages">HTML</span>
            <span className="languages">CSS</span>
            <span className="languages">JavaScript</span>
          </h3>
          <div className="buttons-project">
            <a
              href="https://github.com/juanisabba/simon-says/"
              target="_blank"
              rel="noreferrer"
            >
              Ver en GitHub{" "}
              <FontAwesomeIcon
                icon={faGithub}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
            <a
              href="https://juanisabba.github.io/simon-says/"
              target="_blank"
              rel="noreferrer"
            >
              Ver el sitio web{" "}
              <FontAwesomeIcon
                icon={faExternalLinkAlt}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
