import React from "react";
import '../../styles/error404.css'
import Header from "../components/Header";
import ukFlag from "../../images/uk-flag.png";
import { Link } from "react-router-dom"

export default function Error404() {
  return (
    <div>
      <Header />
      <Link to="/en/error404" className="language-flag">English <img src={ukFlag} alt="English" /></Link>
      <div className="error404">
        <h1>Error 404</h1>
        <h2>Page not found</h2>
        <iframe
          title="404 error"
          src="https://giphy.com/embed/UHAYP0FxJOmFBuOiC2"
          width="246"
          height="185"
          frameBorder="0"
          className="giphy-embed"
        ></iframe>
        <p>
          <a href="https://giphy.com/gifs/gengar-jijidraws-jiji-knight-UHAYP0FxJOmFBuOiC2">
            via GIPHY
          </a>
        </p>
      </div>
    </div>
  );
}
