import React from "react";
import { Link } from "react-router-dom";
import '../../../styles/projects.css'
import portfolio from "../../../images/portfolio.png";
import juaniflix from "../../../images/juaniflix.png";
import simonSays from "../../../images/simon-says.png";
import marioLuigi from "../../../images/mario-luigi.png";
import covidApi from "../../../images/covidapi.png";
import Header from "../../components/Header";
import ukFlag from "../../../images/uk-flag.png";

export default function Projects() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/en/projects" className="language-flag">English <img src={ukFlag} alt="English" /></Link>
      <div className="Projects">
        <div className="container">
          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>01</h2>
              <h3>PORTFOLIO</h3>
              <img src={portfolio} alt="Juaniflix" />
              <p>
                Este es mi portafolio personal en donde están algunos de mis
                proyectos y la información acerca de mí.
              </p>
              <Link to="/projects/portfolio">MÁS INFO</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>02</h2>
              <h3>NETFLIX CLONE</h3>
              <img src={juaniflix} alt="Juaniflix" />
              <p>
                Juaniflix es un clon de Netflix sumamente completo. Creado con
                React,TMDB Api, Swiper y Material UI.
              </p>
              <Link to="/projects/juaniflix">MÁS INFO</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>03</h2>
              <h3>SIMON SAYS</h3>
              <img src={simonSays} alt="Simon Says" />
              <p>
                El popular juego donde tenés que recordar el color y el orden.
                Creado con HTML, CSS y JavaScript.
              </p>
              <Link to="/projects/simon-says">MÁS INFO</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>04</h2>
              <h3>MARIO VS LUIGI</h3>
              <img src={marioLuigi} alt="Mario vs Luigi Pong" />
              <p>
                Mario vs Luigi Pong es my adaptación del famoso juego. Consiste
                en un juego 1 vs 1 creado con HTML, CSS y JavaScript
              </p>
              <Link to="/projects/mario-vs-luigi">MÁS INFO</Link>
            </div>
          </div>

          <div className="card">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <div className="content">
              <h2>05</h2>
              <h3>COVID LIVE DATA</h3>
              <img src={covidApi} alt="Covid Live Data" />
              <p>
                Covid19 Live Data es un sitio web que muestra toda la
                información de cada país en tiempo real usando la API Covid19.
              </p>
              <Link to="/projects/covid-live-data">MÁS INFO</Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
