import React from "react";
import portfolio from "../../../images/portfolio.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import "../../../styles/projects.css";
import Header from "../../components/Header";
import spainFlag from "../../../images/spain-flag.png";
import { Link } from "react-router-dom"

export default function Portfolio() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/projects/portfolio" className="language-flag">Español <img src={spainFlag} alt="Spanish" /></Link>
      <div className="Project">
        <img src={portfolio} alt="Portfolio" className="project-image" />
        <div className="project-info">
          <h3 className="project-text">
            This is my personal portfolio, where there are some of my projects
            and the information about me.
            <br />
            <br />
            <span className="languages">React</span>
          </h3>
          <div className="buttons-project">
            <a
              href="https://github.com/juanisabba/my-app"
              target="_blank"
              rel="noreferrer"
            >
              See it on GitHub{" "}
              <FontAwesomeIcon
                icon={faGithub}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
