import React from "react";
import '../../../styles/projects.css'
import juaniflix from "../../../images/juaniflix.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import Header from "../../components/Header";
import ukFlag from "../../../images/uk-flag.png";
import { Link } from "react-router-dom"

export default function Juaniflix() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/en/projects/juaniflix" className="language-flag">English <img src={ukFlag} alt="English" /></Link>
      <div className="Project">
        <img src={juaniflix} alt="Juaniflx" className="project-image" />
        <div className="project-info">
          <h3 className="project-text">
            Juaniflix es un clon de Netflix sumamente completo. Creado con
            React,TMDB Api, Swiper y Material UI.
            <br />
            <br />
            <span className="languages">React</span>
          </h3>
          <div className="buttons-project">
            <a
              href="https://github.com/juanisabba/juaniflix/"
              target="_blank"
              rel="noreferrer"
            >
              Ver en GitHub{" "}
              <FontAwesomeIcon
                icon={faGithub}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
            <a
              href="https://juanisabba.github.io/juaniflix/"
              target="_blank"
              rel="noreferrer"
            >
              Ver el sitio web{" "}
              <FontAwesomeIcon
                icon={faExternalLinkAlt}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
