import React from "react";
import simonSays from "../../../images/simon-says.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import "../../../styles/projects.css";
import Header from "../../components/Header";
import spainFlag from "../../../images/spain-flag.png";
import { Link } from "react-router-dom"

export default function SimonSays() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/projects/simon-says" className="language-flag">Español <img src={spainFlag} alt="Spanish" /></Link>
      <div className="Project">
        <img
          src={simonSays}
          alt="Simon Says"
          className="project-image"
        />
        <div className="project-info">
          <h3 className="project-text">
            The popular game where you have to remember the color and it's
            order. Created with HTML, CSS and JavaScript. <br />
            <br />
            <span className="languages">HTML</span>
            <span className="languages">CSS</span>
            <span className="languages">JavaScript</span>
          </h3>
          <div className="buttons-project">
            <a
              href="https://github.com/juanisabba/simon-says/"
              target="_blank"
              rel="noreferrer"
            >
              See it on GitHub{" "}
              <FontAwesomeIcon
                icon={faGithub}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
            <a
              href="https://juanisabba.github.io/simon-says/"
              target="_blank"
              rel="noreferrer"
            >
              See the website{" "}
              <FontAwesomeIcon
                icon={faExternalLinkAlt}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
