import React from "react";
import covidApi from "../../../images/covidapi.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import "../../../styles/projects.css";
import Header from "../../components/Header";
import spainFlag from "../../../images/spain-flag.png";
import { Link } from "react-router-dom"

export default function CovidApi() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/projects/covid-data-live" className="language-flag">Español <img src={spainFlag} alt="Spanish" /></Link>
      <div className="Project">
        <img src={covidApi} alt="Covid API" className="project-image" />
        <div className="project-info">
          <h3 className="project-text">
          Covid19 Live Data is a website that shows all the information about the covid in every country in real time
          using the Covid19 API.
            <br />
            <br />
            <span className="languages">HTML</span>
            <span className="languages">CSS</span>
            <span className="languages">JavaScript</span>
          </h3>
          <div className="buttons-project">
            <a
              href="https://github.com/juanisabba/covid19/"
              target="_blank"
              rel="noreferrer"
            >
              See it on GitHub{" "}
              <FontAwesomeIcon
                icon={faGithub}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
            <a
              href="https://juanisabba.github.io/covid19/"
              target="_blank"
              rel="noreferrer"
            >
              See the website{" "}
              <FontAwesomeIcon
                icon={faExternalLinkAlt}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
