import React from "react";
import '../../../styles/projects.css'
import portfolio from "../../../images/portfolio.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import Header from "../../components/Header";
import ukFlag from "../../../images/uk-flag.png";
import { Link } from "react-router-dom"

export default function Portfolio() {
  document.title = "Juani Sabbatini | Projects";
  return (
    <div>
      <Header />
      <Link to="/en/projects/portfolio" className="language-flag">English <img src={ukFlag} alt="English" /></Link>
      <div className="Project">
        <img src={portfolio} alt="Portfolio" className="project-image" />
        <div className="project-info">
          <h3 className="project-text">
            Este es mi portafolio personal en donde están algunos de mis
            proyectos y la información acerca de mí.
            <br />
            <br />
            <span className="languages">React</span>
          </h3>
          <div className="buttons-project">
            <a
              href="https://github.com/juanisabba/my-app"
              target="_blank"
              rel="noreferrer"
            >
              Ver en GitHub{" "}
              <FontAwesomeIcon
                icon={faGithub}
                aria-hidden="true"
                className="button-icon"
              />
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
